/* 1. Global - Set up the things
/* ---------------------------------------------------------- */
@import "global.css";

/* Nanobar Progress */
.my-class .bar {
    background: var(--bold-purple);
    height: 2px;
    /*backgrouf69801;*/
}

body {
    /*background: #f4f8fb;*/
    background: var(--main-background-color);
    color: var(--main-text-color);
}

.img {
    display: block;
    width: 100%;
    height: 100%;
    background-position: center center;
    background-size: cover;
    border-radius: 100%;
}

.hidden {
    visibility: hidden;
    position: absolute;
    text-indent: -9999px;
}


/* 2. Layout - Page building blocks
/* ---------------------------------------------------------- */

.site-wrapper {
    display: flex;
    flex-direction: column;
    min-height: 30vh;
}

.site-main {
    z-index: 100;
    flex-grow: 1;
}

/* Full width page blocks */
.outer {
    position: relative;
    padding: 0 5vw;
}

/* Centered content container blocks */
.inner {
    margin: 0 auto;
    max-width: 1040px;
    width: 100%;
}

/* Usage:

<div class="outer">
    <div class="inner">
        Centered content
    </div>
</div>

*/

