    margin: 0 auto;
    padding: 0 170px 6vw;
    min-height: 230px;
    font-family: Georgia, serif;
    font-size: 2rem;
    line-height: 1.6em;
    background: var(--main-background-color);
}

@media (max-width: 1170px) {
    .post-full-content {
        padding: 0 11vw 0;
    }
}
@media (max-width: 800px) {
    .post-full-content {
        font-size: 1.9rem;
    }
}

