    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 920px;
    color: var(--main-text-color);
}*/

.post-full-content h1,
.post-full-content h2,
.post-full-content h3,
.post-full-content h4,
.post-full-content h5,
.post-full-content h6,
.post-full-content p,
.post-full-content ul,
.post-full-content ol,
.post-full-content dl,
.post-full-content pre,
.post-full-content blockquote,
.post-full-comments,
.footnotes {
    min-width: 100%;
}

.post-full-content li {
    word-break: break-word;
}

.post-full-content li p {
    margin: 0;
}
/*
.post-template .kg-card-markdown > p:first-child {
    font-size: 1.25em;
    line-height: 1.5em;
}*/

.post-full-content a {
    color: var(--main-text-color);
    box-shadow: var(--main-text-color) 0 -1px 0 inset;
}

.post-full-content a:hover {
    color: var(--blue);
    text-decoration: none;
    box-shadow: var(--blue) 0 -1px 0 inset;
}

.post-full-content strong,
.post-full-content em {
    /*color: color(var(--main-text-color) l(-5%));*/
    color: var(--main-text-color);
}

.post-full-content small {
    display: inline-block;
    line-height: 1.6em;
}

.post-full-content li:first-child {
    margin-top: 0;
}

/*.post-full-content img,*/
.post-full-content video {
    display: block;
    margin: 1em;
    max-width: 1040px;
}
@media (max-width: 1040px) {
    /*.post-full-content img,*/
    .post-full-content video {
        width: 100%;
    }
}


img[src~="thumbnail"] {
   width:150px;
   height:auto;
}
img[src~="bordered"] {
   border: 1px solid black;
}

/* Full bleed images (#full)
Super neat trick courtesy of @JoelDrapper

Usage (In Ghost edtior):

![img](/some/image.jpg#full)

*/
.post-full-content img[src$="#full"] {
    max-width: none;
    width: 100vw;
}


/* Image captions

Usage (In Ghost editor):

![img](/some/image.jpg)
<small>Your image caption</small>

*/
.post-full-content img + br + small {
    display: block;
    margin-top: -3em;
    margin-bottom: 1.5em;
}

.align-left {
    display: block;
    margin-left: auto;
    margin-right:auto;
    margin-bottom: 1em;
}

@media (min-width: 37.5em) {
    .align-left {
        float: left;
        margin-right:1em;
    }
}

.align-right {
    display: block;
    margin-left: auto;
    margin-right:auto;
    margin-bottom: 1em;
}

@media (min-width: 37.5em) {
    .align-right {
        float: right;
        margin-left:1em;
    }
}

.align-center {
    display: block;
    margin-left: auto;
    margin-right:auto;
    border: 2px solid;
    border-color: #000;
}

@media (min-width: 64em) {
    .full {
        margin-right:-20.3389830508% !important
    }
}

.post-full-content iframe {
    margin: 0 auto;
}

.post-full-content blockquote {
    margin: 0 0 1.5em;
    padding: 0 1.5em;
    border-left: #3eb0ef 3px solid;
}

.post-full-content blockquote p {
    margin: 0 0 1em 0;
    color: inherit;
    font-size: inherit;
    line-height: inherit;
    font-style: italic;
}

.post-full-content blockquote p:last-child {
    margin-bottom: 0;
}

.post-full-content code {
    padding: 0 5px 2px;
    /*font-size: 0.8em;*/
    line-height: 1em;
    font-weight: 400!important;
    background: var(--main-color-reverse);
    border-radius: 2px;
}

.post-full-content pre {
    overflow-x: auto;
    margin: 1.5em 0 3em;
    padding: 20px;
    max-width: 100%;
    border: color(var(--darkgrey) l(-10%)) 1px solid;
    color: var(--whitegrey);
    font-size: 1.4rem;
    line-height: 1.5em;
    background: color(var(--darkgrey) l(-3%));
    border-radius: 5px;
}

.post-full-content pre code {
    padding: 0;
    font-size: inherit;
    line-height: inherit;
    background: transparent;
}

.post-full-content pre code * {
    color: inherit;
}

.post-full-content .fluid-width-video-wrapper {
    margin: 1.5em 0 3em;
}

.post-full-content hr {
    margin: 4vw 0;
}

.post-full-content hr:after {
    content: "";
    position: absolute;
    top: -15px;
    left: 50%;
    display: block;
    margin-left: -10px;
    width: 1px;
    height: 30px;
    /*background: color(var(--main-background-color) l(+95%));*/
    /*background: color(var(--main-text-color) l(+50%));*/
    background: var(--main-text-color);
    /*box-shadow: #000 0 0 0 5px;*/
    box-shadow: var(--main-text-color) 0 0 2px;
    transform: rotate(45deg);
}

