        margin-top: 0.2em;
        font-size: 4.2rem;
        line-height: 1.05em;
    }
    .post-full-image {
        margin-top: 5px;
        margin-bottom: 5vw;
    }
    .post-full-content {
        padding: 0;
    }
    .post-full-content:before,
    .post-full-content:after {
