/* Variables
/* ---------------------------------------------------------- */

:root {
    --blue: #3eb0ef;
    --green: #a4d037;
    --purple: #ad26b4;
    --yellow: #fecd35;
    --red: #f05230;
    --darkgrey: #15171A;
    --midgrey: #738a94;
    --lightgrey: #c5d2d9;
    --whitegrey: #e5eff5;
    --pink: #fa3a57;
    --brown: #a3821a;
    --white: #fff;
    --black: #000;
    --nav-text-color: hsla(0,0%,100%,.85);
  }
[data-theme="dark"] {
    /* Dark Mode Colors*/
    --bold-color: rgb(188, 188, 188);
    --bold-purple: #aa00ff;
    --bold-green: hsl(144, 82%, 42%);
    --box-background-color: hsl(0, 0%, 30%);
    --highlight-color: hsl(0, 0%, 60%);
    --light-border-color: hsl(0, 0%, 30%);
    --light-text-color: rgb(199, 199, 204);
    --link-color: hsl(178, 83%, 44%);
    --link-color-visited: hsl(178, 64%, 50%);
    --main-color: #fff;
    --main-color-reverse: #000;
    /*--main-background-color: rgb(40, 40, 42);*/
    --main-background-color: #191b1f;
    --main-header-color: hsla(0,0%,100%,.9);
    --main-text-color: hsla(0,0%,100%,.75);
    --medium-text-color: #A3B1B8; /* Cyan Blue*/
    /*--medium-text-color: hsl(0, 0%, 70%);*/
    --underline-color: hsl(0, 0%, 50%);
    --social-link-color: #ffffff;
    --card-background-color: #191b1f;
    --newsletter-background-color: #222324;
    --button-background-color: #111;
    --gradient: #15171A;
}
[data-theme="light"] {
    --bold-color: rgb(0, 0, 0);
    --bold-purple: #b125fc;
    --bold-green: #013220;
    --box-background-color: rgb(249, 249, 250);
    --highlight-color: hsl(0, 0%, 80%);
    --light-border-color: hsl(0, 0%, 30%);
    --light-text-color: rgb(72, 72, 74);
    --link-color: rgb(0, 112, 201);
    --link-color-visited: hsl(250, 100%, 37%);
    --main-color: #000;
    --main-color-reverse: #fff;
    --main-background-color: #fff;
    /*--main-background-color: #ecd59f;*/
    /*--main-background-color: #D3D3D3;*/
    --main-header-color: hsla(0,0%,0%,.9);
    --main-text-color: rgb(51, 51, 51);
    --medium-text-color: hsl(0, 40%, 2%);
    --sidebar-color: hsl(0, 0%, 90%);
    --underline-color: hsl(0, 0%, 60%);
    --social-link-color: #262222;
    --card-background-color: rgb(250, 250, 250);
    --newsletter-background-color: #00000008;
    --button-background-color: #fff;
    --gradient: #D5D8DD;
}

@media (prefers-color-scheme: dark) {
     /* Dark Mode */
    html,
    html[data-theme="dark"] {
      --bold-color: rgb(188, 188, 188);
      --bold-purple: #aa00ff;
      --bold-green: hsl(144, 82%, 42%);
      --box-background-color: hsl(0, 0%, 30%);
      --highlight-color: hsl(0, 0%, 60%);
      --light-border-color: hsl(0, 0%, 30%);
      --light-text-color: rgb(199, 199, 204);
      --link-color: hsl(178, 83%, 44%);
      --link-color-visited: hsl(178, 64%, 50%);
      --main-color: #fff;
      --main-color-reverse: #000;
      /*--main-background-color: rgb(40, 40, 42);*/
      --main-background-color: #191b1f;
      --main-header-color: hsla(0,0%,100%,.9);
      --main-text-color: hsla(0,0%,100%,.75);
      --medium-text-color: #A3B1B8; /* Cyan Blue*/
      /*--medium-text-color: hsl(0, 0%, 70%);*/
      --underline-color: hsl(0, 0%, 50%);
      --social-link-color: #ffffff;
      --card-background-color: #191b1f;
      --newsletter-background-color: #222324;
      --button-background-color: #111;
      --gradient: #15171A;
    }

    /* Light Mode */
    html[data-theme="light"] {
      --bold-color: rgb(0, 0, 0);
      --bold-purple: #b125fc;
      --bold-green: #013220;
      --box-background-color: rgb(249, 249, 250);
      --highlight-color: hsl(0, 0%, 80%);
      --light-border-color: hsl(0, 0%, 75%);
      --light-text-color: rgb(72, 72, 74);
      --link-color: rgb(0, 112, 201);
      --link-color-visited: hsl(250, 100%, 37%);
      --main-color: #000;
      --main-color-reverse: #fff;
      --main-background-color: #fff;
      /*--main-background-color: #ecd59f;*/
      /*--main-background-color: #D3D3D3;*/
      --main-header-color: hsla(0,0%,0%,.9);
      --main-text-color: rgb(51, 51, 51);
      --medium-text-color: hsl(0, 40%, 2%);
      --sidebar-color: hsl(0, 0%, 90%);
      --underline-color: hsl(0, 0%, 60%);
      --social-link-color: #262222;
      --card-background-color: rgb(250, 250, 250);
      --newsletter-background-color: #00000008;
      --button-background-color: #fff;
      --gradient: #D5D8DD;
    }
}

/* Reset
/* ---------------------------------------------------------- */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    font-size: 100%;
    vertical-align: baseline;
}
body {
    line-height: 1;
}
ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    border-spacing: 0;
    border-collapse: collapse;
}
img {
    max-width: 100%;
}
html {
    box-sizing: border-box;
    font-family: sans-serif;

    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
a {
    background-color: transparent;
}
a:active,
a:hover {
    outline: 0;
}
b,
strong {
    font-weight: bold;
}
i,
em,
dfn {
    font-style: italic;
}
h1 {
    margin: 0.67em 0;
    font-size: 2em;
}
small {
    font-size: 80%;
}
sub,
sup {
    position: relative;
    font-size: 75%;
    line-height: 0;
    vertical-align: baseline;
}
sup {
    top: -0.5em;
}
sub {
    bottom: -0.25em;
}
img {
    border: 0;
    height: auto;
    vertical-align: middle;
    width: auto\9;

}
svg:not(:root) {
    overflow: hidden;
}
mark {
    background-color: #fdffb6;
}
code,
kbd,
pre,
samp {
    font-family: monospace, monospace;
    font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
    margin: 0; /* 3 */
    color: inherit; /* 1 */
    font: inherit; /* 2 */
}
button {
    overflow: visible;
    border: none;
}
button,
select {
    text-transform: none;
}
button,
html input[type="button"],
/* 1 */
input[type="reset"],
input[type="submit"] {
    cursor: pointer; /* 3 */

    -webkit-appearance: button; /* 2 */
}
button[disabled],
html input[disabled] {
    cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
    padding: 0;
    border: 0;
}
input {
    line-height: normal;
}
input:focus {
    outline: none;
}
input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    height: auto;
}
input[type="search"] {
    box-sizing: content-box; /* 2 */

    -webkit-appearance: textfield; /* 1 */
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}
legend {
    padding: 0; /* 2 */
    border: 0; /* 1 */
}
textarea {
    overflow: auto;
}
table {
    border-spacing: 0;
    border-collapse: collapse;
}
td,
th {
    padding: 0;
}


/* ==========================================================================
   Base styles: opinionated defaults
   ========================================================================== */

html {
    overflow-x: hidden;
    overflow-y: scroll;
    font-size: 62.5%;

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
    overflow-x: hidden;
    /*color: color(var(--midgrey) l(-25%));*/
    /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;*/
    color: var(--main-text-color);
    font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
    font-size: 1.5rem;
    line-height: 1.6em;
    font-weight: 400;
    font-style: normal;
    letter-spacing: 0;
    text-rendering: optimizeLegibility;
    background: var(--main-background-color);
    /*background: #000;*/

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -moz-font-feature-settings: "liga" on;
}

::selection {
    text-shadow: none;
    background: color(var(--blue) lightness(+30%));
}

hr {
    position: relative;
    display: block;
    width: 100%;
    margin: 2.5em 0 3.5em;
    padding: 0;
    height: 1px;
    border: 0;
    /*border-top: 1px solid color(var(--lightgrey) l(+10%));*/
    border-top: 1px solid var(--main-text-color);
}

audio,
canvas,
iframe,
img,
svg,
video {
    vertical-align: middle;
}

fieldset {
    margin: 0;
    padding: 0;
    border: 0;
}

textarea {
    resize: vertical;
}

p,
ul,
ol,
dl,
blockquote {
    margin: 0 0 1.5em 0;
}

ol,
ul {
    padding-left: 1.3em;
    padding-right: 1.5em;
}

ol ol,
ul ul,
ul ol,
ol ul {
    margin: 0.5em 0 1em;
}

ul {
    list-style: disc;
}

ol {
    list-style: decimal;
}

li {
    margin: 0.5em 0;
    padding-left: 0.3em;
    line-height: 1.6em;
}

dt {
    float: left;
    margin: 0 20px 0 0;
    width: 120px;
    color: var(--darkgrey);
    font-weight: 500;
    text-align: right;
}

dd {
    margin: 0 0 5px 0;
    text-align: left;
}

blockquote {
    margin: 1.5em 0;
    padding: 0 1.6em 0 1.6em;
    border-left: var(--whitegrey) 0.5em solid;
}

blockquote p {
    margin: 0.8em 0;
    font-size: 1.2em;
    font-weight: 300;
}

blockquote small {
    display: inline-block;
    margin: 0.8em 0 0.8em 1.5em;
    font-size: 0.9em;
    opacity: 0.8;
}
/* Quotation marks */
blockquote small:before {
    content: "\2014 \00A0";
}

blockquote cite {
    font-weight: bold;
}
blockquote cite a {
    font-weight: normal;
}

a {
    color: color(var(--blue) l(-5%));
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-top: 0;
    line-height: 1.15;
    font-weight: 700;
    text-rendering: optimizeLegibility;
}

h1 {
    margin: 0 0 0.5em 0;
    font-size: 5rem;
    font-weight: 700;
}
@media (max-width: 500px) {
    h1 {
        font-size: 2.2rem;
    }
}

h2 {
    margin: 1.5em 0 0.5em 0;
    font-size: 2rem;
}
@media (max-width: 500px) {
    h2 {
        font-size: 1.8rem;
    }
}

h3 {
    margin: 1.5em 0 0.5em 0;
    font-size: 1.8rem;
    font-weight: 500;
}
@media (max-width: 500px) {
    h3 {
        font-size: 1.7rem;
    }
}

h4 {
    margin: 1.5em 0 0.5em 0;
    font-size: 1.6rem;
    font-weight: 500;
}

h5 {
    margin: 1.5em 0 0.5em 0;
    font-size: 1.4rem;
    font-weight: 500;
}

h6 {
    margin: 1.5em 0 0.5em 0;
    font-size: 1.4rem;
    font-weight: 500;
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast:active) {
    p, ol, ul{
        width: 100%;
    }
    blockquote {
        width: 100%;
    }
}
